import React, { useState, useEffect, useRef, forwardRef} from 'react';
import { useLocation, useNavigate, useParams } from "react-router-dom";

import PlainContainer from '../../../../../../../components/general/containers/PlainContainer';
import Section from '../../../../../../../components/general/containers/Section';
import Form from '../../../../../../../components/general/forms/core/Form';
import FormActions from '../../../../../../../components/general/forms/core/FormActions';
import TextInput from '../../../../../../../components/general/forms/TextInput';
import FileInput from '../../../../../../../components/general/forms/FileInput';

import axios from 'axios';

import { useSelector } from 'react-redux';
import { API_BASE_URL, LOGGER } from '../../../../../../../config';
import { notifyError, notifySuccess } from '../../../../../../../utils/toastify';
import { REQUEST_ERROR, CATEGORY_ADDED } from '../../../../../../../utils/messages';

const AddProductAccessoryPage = () => {
    let navigate = useNavigate();
    const location = useLocation();
    let { product_id } = useParams();

    const [routeArray, setRouteArray] = useState(location.pathname.substring(1).split('/'));
    const [routeText, setRouteText] = useState('');

    useEffect(() => {
        let tempRoute = '';
        routeArray.map(route => {
            tempRoute = tempRoute.concat(`${route} / `);
            tempRoute = tempRoute.replace('-', ' ');
        });
        setRouteText(removeLastChars(tempRoute, 3));
    }, []);

    let removeLastChars = (str, chars) => {
        return str.substring(0, str.length - chars);
    }

    const admin = useSelector((state) => state.admin.value);

    const config = {
        headers: {
        'Authorization': admin.access_token
        }
    }

    const [title, setTitle] = useState('');
    const [image, setImage] = useState('');
    const [imageID, setImageID] = useState('');

    const [isTitleError, setIsTitleError] = useState(false);
    const [isImageError, setIsImageError] = useState(false);

    const [titleErrorTxt, setTitleErrorTxt] = useState("");
    const [imageErrorTxt, setImageErrorTxt] = useState("");

    const [resetAttachment, setResetAttachment] = useState(false);

    const submitEditThisForm = async (e) => {
        e.preventDefault();

        if(title == ''){
            setTitleErrorTxt('Title is required');
            setIsTitleError(true);
            return;
        }
        else{
            setTitleErrorTxt('');
            setIsTitleError(false);
        }

        if(imageID == ''){
            setImageErrorTxt('Image is required');
            setIsImageError(true);
            return;
        }
        else{
            setImageErrorTxt('');
            setIsImageError(false);
        }

        const data = {
            title: title,
            image: imageID,
        }

        try {
            const createProductReq = await axios.post(`${API_BASE_URL}product/product/accessory/${product_id}`, data, config);
            notifySuccess(CATEGORY_ADDED);
            navigate(`/dashboard/pages/products/products/details/${product_id}`);
            LOGGER &&  console.log(createProductReq);
        }
        catch (error) {
            notifyError(REQUEST_ERROR);
            LOGGER &&  console.log(error);
        }
    }

    const resetForm = () => {
        
    }

    return (
        <Section heading="Accessories" description={routeText} action={() => navigate(`/dashboard/pages/products/products/details/${product_id}`)} actionLable="back" showAction={true}>
            <PlainContainer styles={{width: '100%', display: 'flex', flexDirection: 'column', position: 'static'}}>
                <form onSubmit={(e) => submitEditThisForm(e)} noValidate>
                    <Form>
                        <TextInput lable="title" isMandatory={true} isError={isTitleError} error={titleErrorTxt}>
                            <input type="text" className="formStepInputTxt capitalize" value={title} placeholder="Please Enter The Title" onChange={(e) => setTitle(e.target.value)} />
                        </TextInput>
                        <FileInput lable="image" isMandatory={true} value={imageID} preview={image} onchange={(data) => {setImageID(data); LOGGER &&  console.log(data)}} isError={isImageError} error={imageErrorTxt} reset={resetAttachment} />
                        <FormActions isPrimary={true} isSecondary={true} primaryLable="submit" secondaryLable="reset" secondaryAction={() => resetForm()} />
                    </Form>
                </form>
            </PlainContainer>
        </Section>
    )
}

export default AddProductAccessoryPage