import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { useLocation, useNavigate, useParams } from 'react-router-dom';

import DetailSection from '../../../../../components/dashboard/common/detailSection/DetailSection';
import DetailSectionContent from '../../../../../components/dashboard/common/detailSection/DetailSectionContent';
import DetailSectionRow from '../../../../../components/dashboard/common/detailSection/DetailSectionRow';

import NoDataContainer from '../../../../../components/general/containers/NoDataContainer';
import PlainContainer from '../../../../../components/general/containers/PlainContainer';
import Section from '../../../../../components/general/containers/Section';
import { API_BASE_URL, LOGGER } from '../../../../../config';

import { PRODUCT_VARIANT_CREATION_FAILED, PRODUCT_VARIANT_DELETED, REQUEST_ERROR } from '../../../../../utils/messages';
import { notifyError, notifySuccess } from '../../../../../utils/toastify';

const HomeBannerPage = () => {
    const admin = useSelector((state) => state.admin.value);
    let navigate = useNavigate();
    const location = useLocation();
    const [routeArray, setRouteArray] = useState(location.pathname.substring(1).split('/'));
    const [routeText, setRouteText] = useState('');
    const [homeBanner, setHomeBanner] = useState({});
  
    useEffect(() => {
        let tempRoute = '';
        routeArray.map(route => {
            tempRoute = tempRoute.concat(`${route} / `);
            tempRoute = tempRoute.replace('-', ' ');
        });
        setRouteText(removeLastChars(tempRoute, 3));
    }, []);
  
    let removeLastChars = (str, chars) => {
        return str.substring(0, str.length - chars);
    }
  
    const config = {
        headers: {
            'Authorization': admin.access_token
        }
    }

    const getHomeBannerHandler = async () => {
        try {
            const getHomeBannerReq = await axios.get(`${API_BASE_URL}home/banner`);
            console.log(getHomeBannerReq.data);
            setHomeBanner(getHomeBannerReq.data);
        }
        catch (error) {
            if(error.response.status === 404){
                console.log('not found');
            }
        }
    }

    useEffect(() => {
        getHomeBannerHandler();
    }, []);

    return (
        <>
            <Section heading="banner" description={routeText} action={() => navigate("/dashboard/pages/home")} actionLable="back" showAction={true}>
                <PlainContainer type="fullwidth">
                    <DetailSection lable="banner" hasPrimaryAction={true} lablePrimaryAction={`edit`} primaryAction={() => navigate("/dashboard/pages/home/banner/edit")} hasSecondaryAction={false}>
                        <DetailSectionContent hasPadding={true}>
                            <DetailSectionRow lable={`title`} value={homeBanner.title} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`description`} value={homeBanner.description} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`primary button lable`} value={homeBanner.primary_button_lable} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`primary button URL`} value={homeBanner.primary_button_url} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`secondary button lable`} value={homeBanner.secondary_button_lable} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`secondary button URL`} value={homeBanner.secondary_button_url} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`featured image`} value={homeBanner.media?.url} isImage={true} isIcon={false} hasChildren={false} />
                        </DetailSectionContent>
                    </DetailSection>
                </PlainContainer>
            </Section>
        </>
    )
}

export default HomeBannerPage