import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { useLocation, useNavigate, useParams } from 'react-router-dom';

import DetailSection from '../../../../../components/dashboard/common/detailSection/DetailSection';
import DetailSectionContent from '../../../../../components/dashboard/common/detailSection/DetailSectionContent';
import DetailSectionRow from '../../../../../components/dashboard/common/detailSection/DetailSectionRow';

import NoDataContainer from '../../../../../components/general/containers/NoDataContainer';
import PlainContainer from '../../../../../components/general/containers/PlainContainer';
import Section from '../../../../../components/general/containers/Section';
import { API_BASE_URL, LOGGER } from '../../../../../config';

import { OPENING_DELETED, REQUEST_ERROR } from '../../../../../utils/messages';
import { notifyError, notifySuccess } from '../../../../../utils/toastify';

const HomeTestimonialsPage = () => {
    const admin = useSelector((state) => state.admin.value);
    let navigate = useNavigate();
    const location = useLocation();
    const [routeArray, setRouteArray] = useState(location.pathname.substring(1).split('/'));
    const [routeText, setRouteText] = useState('');
    const [testimonials, setTestimonials] = useState([]);
  
    useEffect(() => {
        let tempRoute = '';
        routeArray.map(route => {
            tempRoute = tempRoute.concat(`${route} / `);
            tempRoute = tempRoute.replace('-', ' ');
        });
        setRouteText(removeLastChars(tempRoute, 3));
    }, []);
  
    let removeLastChars = (str, chars) => {
        return str.substring(0, str.length - chars);
    }
  
    const config = {
        headers: {
            'Authorization': admin.access_token
        }
    }

    const getTestimonialsHandler = async () => {
        try {
            const getTestimonialsReq = await axios.get(`${API_BASE_URL}home/testimonials`);
            console.log(getTestimonialsReq.data);
            setTestimonials(getTestimonialsReq.data);
        }
        catch (error) {
            if(error.response.status === 404){
                console.log('not found');
            }
        }
    }

    useEffect(() => {
        getTestimonialsHandler();
    }, []);

    const deleteThisTestimonyHandler = async (id) => {
        try {
            const deleteThisTestimonyReq = await axios.delete(`${API_BASE_URL}home/testimonials/${id}`, config);
            console.log(deleteThisTestimonyReq.data);
            getTestimonialsHandler();
            notifySuccess(OPENING_DELETED);
        }
        catch (error) {
            if(error.response.status === 404){
                console.log('not found');
            }
        }
    }

    return (
        <>
            <Section heading="Testimonials" description={routeText} action={() => navigate("/dashboard/pages/home")} actionLable="back" showAction={true}>
                <PlainContainer type="fullwidth">
                    <DetailSection lable="List of Testimonials" hasPrimaryAction={true} lablePrimaryAction={`create`} primaryAction={() => navigate("/dashboard/pages/home/testimonials/create")} hasSecondaryAction={false}>
                        <div style={{paddingTop: `20px`, paddingLeft: `20px`, paddingRight: `20px`}}>
                            {
                                testimonials.map(testimony => {
                                    return (
                                        <div style={{width: `100%`, float: `left`, marginBottom: `20px`}} key={testimony.id}>
                                            <DetailSection lable={testimony.name} hasPrimaryAction={true} lablePrimaryAction={`edit`} primaryAction={() => navigate(`/dashboard/pages/home/testimonials/edit/${testimony.id}`)} hasSecondaryAction={true} lableSecondaryAction={`delete`} secondaryAction={() => deleteThisTestimonyHandler(testimony.id)}>
                                                <DetailSectionContent hasPadding={true}>
                                                    <DetailSectionRow lable={`description`} value={testimony.description} isImage={false} isIcon={false} hasChildren={false} />
                                                    <DetailSectionRow lable={`location`} value={testimony.location} isImage={false} isIcon={false} hasChildren={false} />
                                                </DetailSectionContent>
                                            </DetailSection>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </DetailSection>
                </PlainContainer>
            </Section>
        </>
    )
}

export default HomeTestimonialsPage