import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { useLocation, useNavigate, useParams } from "react-router-dom";

import DetailSection from '../../../../../../components/dashboard/common/detailSection/DetailSection';
import DetailSectionContent from '../../../../../../components/dashboard/common/detailSection/DetailSectionContent';
import DetailSectionRow from '../../../../../../components/dashboard/common/detailSection/DetailSectionRow';

import Table from '../../../../../../components/dashboard/common/table/Table';
import Heading from '../../../../../../components/dashboard/common/table/Heading';
import HeadingItem from '../../../../../../components/dashboard/common/table/HeadingItem';
import TableRow from '../../../../../../components/dashboard/common/table/TableRow';
import RowItem from '../../../../../../components/dashboard/common/table/RowItem';
import RowIcon from '../../../../../../components/dashboard/common/table/RowIcon';
import TableButton from '../../../../../../components/dashboard/common/table/TableButton';

import NoDataContainer from '../../../../../../components/general/containers/NoDataContainer';
import PlainContainer from '../../../../../../components/general/containers/PlainContainer';
import Section from '../../../../../../components/general/containers/Section';
import { API_BASE_URL, LOGGER } from '../../../../../../config';
import { notifyError, notifySuccess } from '../../../../../../utils/toastify';
import { REQUEST_ERROR } from '../../../../../../utils/messages';
import RowImage from '../../../../../../components/dashboard/common/table/RowImage';

const ProductDetailsPage = () => {
    const admin = useSelector((state) => state.admin.value);
    let { product_id } = useParams();

    let navigate = useNavigate();
    const location = useLocation();
    const [routeArray, setRouteArray] = useState(location.pathname.substring(1).split('/'));
    const [routeText, setRouteText] = useState('');
    const [product, setProduct] = useState({});
    const [showProduct, setShowProduct] = useState(false);

    useEffect(() => {
        let tempRoute = '';
        routeArray.map(route => {
            tempRoute = tempRoute.concat(`${route} / `);
            tempRoute = tempRoute.replace('-', ' ');
        });
        setRouteText(removeLastChars(tempRoute, 3));
    }, []);

    let removeLastChars = (str, chars) => {
        return str.substring(0, str.length - chars);
    }

    const config = {
        headers: {
            'Authorization': admin.access_token
        }
    }

    const getThisProductHandler = async () => {
        try {
            const getProductReq = await axios.get(`${API_BASE_URL}product/product/${product_id}`);
            
            setProduct(getProductReq.data);
            console.log("comming data ", getProductReq.data)
            if (getProductReq.data) {
                setShowProduct(true);
            }
            else {
                setShowProduct(false);
            }
        }
        catch (error) {
            if (error.response.status === 404) {
                console.log('not found');
            }
        }
    }

    useEffect(() => {
        getThisProductHandler();
    }, []);

    const deleteHighlight = async (highlight_id) => {
        try {
            const deleteProductHighlightReq = await axios.delete(`${API_BASE_URL}product/product/highlight/${highlight_id}`, config);
            //notifySuccess(CATEGORY_ADDED);
            getThisProductHandler();
            LOGGER && console.log(deleteProductHighlightReq);
        }
        catch (error) {
            notifyError(REQUEST_ERROR);
            LOGGER && console.log(error);
        }
    }

    const deletePdf = async (pdf_id) => {
        try {
            const deleteProductPdfReq = await axios.delete(`${API_BASE_URL}product/product/pdf/${pdf_id}`, config);
            //notifySuccess(CATEGORY_ADDED);
            getThisProductHandler();
            LOGGER && console.log(deleteProductPdfReq);
        }
        catch (error) {
            notifyError(REQUEST_ERROR);
            LOGGER && console.log(error);
        }
    }

    const deleteAccessory = async (accessory_id) => {
        try {
            const deleteProductAccessoryReq = await axios.delete(`${API_BASE_URL}product/product/accessory/${accessory_id}`, config);
            //notifySuccess(CATEGORY_ADDED);
            getThisProductHandler();
            LOGGER && console.log(deleteProductAccessoryReq);
        }
        catch (error) {
            notifyError(REQUEST_ERROR);
            LOGGER && console.log(error);
        }
    }

    return (
        <>
            <Section heading="Product Details" description={routeText} action={() => navigate(`/dashboard/pages/products/products/edit/${product_id}`)} actionLable="edit" showAction={true}>
                <PlainContainer type="fullwidth">
                    {
                        showProduct === true
                            ?
                            <>
                                <DetailSection lable="Basic Details" hasPrimaryAction={false} lablePrimaryAction={`edit`} primaryAction={() => navigate("/dashboard/pages/products/products/edit")} hasSecondaryAction={false}>
                                    <DetailSectionContent hasPadding={true}>
                                        <DetailSectionRow lable={`name`} value={product.product.name} isImage={false} isIcon={false} hasChildren={false} />
                                        <DetailSectionRow lable={`category`} value={product.product_category.category.name} isImage={false} isIcon={false} hasChildren={false} />
                                        <DetailSectionRow lable={`description`} value={product.product.description} isImage={false} isIcon={false} hasChildren={false} />
                                        <DetailSectionRow lable={`SEO title`} value={product.seo.title} isImage={false} isIcon={false} hasChildren={false} />
                                        <DetailSectionRow lable={`SEO page URL`} value={product.seo.slug} isImage={false} isIcon={false} hasChildren={false} />
                                        <DetailSectionRow lable={`SEO description`} value={product.seo.description} isImage={false} isIcon={false} hasChildren={false} />
                                        <DetailSectionRow lable={`SEO keywords`} value={product.seo.keywords} isImage={false} isIcon={false} hasChildren={false} />
                                        <DetailSectionRow lable={`featured image`} value={product.product.featured_image?.url} isImage={true} isIcon={false} hasChildren={false} />
                                    </DetailSectionContent>
                                </DetailSection>

                                <PlainContainer type={`fullwidth`} styles={{ marginTop: `25px` }}>
                                    <DetailSection lable="Product Highlights" hasPrimaryAction={true} lablePrimaryAction={`add new`} primaryAction={() => navigate(`/dashboard/pages/products/products/highlights/create/${product_id}`)} hasSecondaryAction={false}>
                                        <DetailSectionContent hasPadding={true}>
                                            <Table>
                                                <Heading>
                                                    <HeadingItem size="1" isIcon={false} icon="" lable={`sl no`} />
                                                    <HeadingItem size="4" isIcon={false} icon="" lable={`title`} />
                                                    <HeadingItem size="2" isIcon={false} icon="" lable={`action`} />
                                                </Heading>
                                                <PlainContainer type="fullwidth">
                                                    {
                                                        product.highlights.map((highlight, i) => {
                                                            return (
                                                                <TableRow key={highlight.id}>
                                                                    <RowItem size="1" lable={i + 1} isCenter={true} hasChildren={false}></RowItem>
                                                                    <RowItem size="4" lable={highlight.description} isCenter={false} hasChildren={false}></RowItem>
                                                                    <RowItem size="2" lable="3" isCenter={false} hasChildren={true}>
                                                                        <TableButton styles={{ width: 'calc(50% - 5px)', marginRight: '10px' }} action={() => navigate(`/dashboard/pages/products/products/highlights/edit/${highlight.id}`)} type="primary">edit</TableButton>
                                                                        <TableButton styles={{ width: 'calc(50% - 5px)' }} action={() => deleteHighlight(highlight.id)} type="danger">delete</TableButton>
                                                                    </RowItem>
                                                                </TableRow>
                                                            )
                                                        })
                                                    }
                                                </PlainContainer>
                                            </Table>
                                        </DetailSectionContent>
                                    </DetailSection>
                                </PlainContainer>

                                <PlainContainer type={`fullwidth`} styles={{ marginTop: `25px` }}>
                                    <DetailSection lable="Product PDF's" hasPrimaryAction={true} lablePrimaryAction={`add new`} primaryAction={() => navigate(`/dashboard/pages/products/products/pdfs/create/${product_id}`)} hasSecondaryAction={false}>
                                        <DetailSectionContent hasPadding={true}>
                                            <Table>
                                                <Heading>
                                                    <HeadingItem size="1" isIcon={false} icon="" lable={`sl no`} />
                                                    <HeadingItem size="4" isIcon={false} icon="" lable={`title`} />
                                                    <HeadingItem size="1" isIcon={false} icon="" lable={``} />
                                                    <HeadingItem size="2" isIcon={false} icon="" lable={`action`} />
                                                </Heading>
                                                <PlainContainer type="fullwidth">
                                                    {
                                                        product.pdfs.map((pdf, i) => {
                                                            return (
                                                                <TableRow key={pdf.id}>
                                                                    <RowItem size="1" lable={i + 1} isCenter={true} hasChildren={false}></RowItem>
                                                                    <RowItem size="4" lable={pdf.title} isCenter={false} hasChildren={false}></RowItem>
                                                                    <RowItem size="1" isCenter={false} hasChildren={true}>
                                                                        <RowIcon name="fa-solid fa-eye" action={() => window.open(pdf.pdf_url?.url)} styles={{ cursor: 'pointer' }} />
                                                                    </RowItem>
                                                                    <RowItem size="2" lable="3" isCenter={false} hasChildren={true}>
                                                                        <TableButton styles={{ width: 'calc(50% - 5px)', marginRight: '10px' }} action={() => navigate(`/dashboard/pages/products/products/pdfs/edit/${pdf.id}`)} type="primary">edit</TableButton>
                                                                        <TableButton styles={{ width: 'calc(50% - 5px)' }} action={() => deletePdf(pdf.id)} type="danger">delete</TableButton>
                                                                    </RowItem>
                                                                </TableRow>
                                                            )
                                                        })
                                                    }
                                                </PlainContainer>
                                            </Table>
                                        </DetailSectionContent>
                                    </DetailSection>
                                </PlainContainer>

                                <PlainContainer type={`fullwidth`} styles={{ marginTop: `25px` }}>
                                    <DetailSection lable="Product Accessories" hasPrimaryAction={true} lablePrimaryAction={`add new`} primaryAction={() => navigate(`/dashboard/pages/products/products/accessories/create/${product_id}`)} hasSecondaryAction={false}>
                                        <DetailSectionContent hasPadding={true}>
                                            <Table>
                                                <Heading>
                                                    <HeadingItem size="1" isIcon={false} icon="" lable={`sl no`} />
                                                    <HeadingItem size="4" isIcon={false} icon="" lable={`title`} />
                                                    <HeadingItem size="1" isIcon={false} icon="" lable={``} />
                                                    <HeadingItem size="2" isIcon={false} icon="" lable={`action`} />
                                                </Heading>
                                                <PlainContainer type="fullwidth">
                                                    {
                                                        product.accessories.map((accessory, i) => {
                                                            return (
                                                                <TableRow key={accessory.id}>
                                                                    <RowItem size="1" lable={i + 1} isCenter={true} hasChildren={false}></RowItem>
                                                                    <RowItem size="4" lable={accessory.title} isCenter={false} hasChildren={false}></RowItem>
                                                                    <RowItem size="1" isCenter={false} hasChildren={true}>
                                                                        <RowImage src={accessory.image_url} />
                                                                    </RowItem>
                                                                    <RowItem size="2" lable="3" isCenter={false} hasChildren={true}>
                                                                        <TableButton styles={{ width: 'calc(50% - 5px)', marginRight: '10px' }} action={() => navigate(`/dashboard/pages/products/products/accessories/edit/${accessory.id}`)} type="primary">edit</TableButton>
                                                                        <TableButton styles={{ width: 'calc(50% - 5px)' }} action={() => deleteAccessory(accessory.id)} type="danger">delete</TableButton>
                                                                    </RowItem>
                                                                </TableRow>
                                                            )
                                                        })
                                                    }
                                                </PlainContainer>
                                            </Table>
                                        </DetailSectionContent>
                                    </DetailSection>
                                </PlainContainer>

                            </>
                            :
                            <></>
                    }

                </PlainContainer>
            </Section>
        </>
    )
}

export default ProductDetailsPage