import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { useLocation, useNavigate, useParams } from 'react-router-dom';

import DetailSection from '../../../../../components/dashboard/common/detailSection/DetailSection';
import DetailSectionContent from '../../../../../components/dashboard/common/detailSection/DetailSectionContent';
import DetailSectionRow from '../../../../../components/dashboard/common/detailSection/DetailSectionRow';

import NoDataContainer from '../../../../../components/general/containers/NoDataContainer';
import PlainContainer from '../../../../../components/general/containers/PlainContainer';
import Section from '../../../../../components/general/containers/Section';
import { API_BASE_URL, LOGGER } from '../../../../../config';

import { PRODUCT_VARIANT_CREATION_FAILED, PRODUCT_VARIANT_DELETED, REQUEST_ERROR } from '../../../../../utils/messages';
import { notifyError, notifySuccess } from '../../../../../utils/toastify';

const HomeSpecializationsPage = () => {
    const admin = useSelector((state) => state.admin.value);
    let navigate = useNavigate();
    const location = useLocation();
    const [routeArray, setRouteArray] = useState(location.pathname.substring(1).split('/'));
    const [routeText, setRouteText] = useState('');
    const [homeSpecializations, setHomeSpecializations] = useState({});
  
    useEffect(() => {
        let tempRoute = '';
        routeArray.map(route => {
            tempRoute = tempRoute.concat(`${route} / `);
            tempRoute = tempRoute.replace('-', ' ');
        });
        setRouteText(removeLastChars(tempRoute, 3));
    }, []);
  
    let removeLastChars = (str, chars) => {
        return str.substring(0, str.length - chars);
    }
  
    const config = {
        headers: {
            'Authorization': admin.access_token
        }
    }

    const getHomeSpecializationsHandler = async () => {
        try {
            const getHomeSpecializationsReq = await axios.get(`${API_BASE_URL}home/specializations`);
            console.log(getHomeSpecializationsReq.data);
            setHomeSpecializations(getHomeSpecializationsReq.data);
        }
        catch (error) {
            if(error.response.status === 404){
                console.log('not found');
            }
        }
    }

    useEffect(() => {
        getHomeSpecializationsHandler();
    }, []);

    return (
        <>
            <Section heading="Specializations" description={routeText} action={() => navigate("/dashboard/pages/home")} actionLable="back" showAction={true}>
                <PlainContainer type="fullwidth">
                    <DetailSection lable="Specializations" hasPrimaryAction={true} lablePrimaryAction={`edit`} primaryAction={() => navigate("/dashboard/pages/home/specializations/edit")} hasSecondaryAction={false}>
                        <DetailSectionContent hasPadding={true}>
                            <DetailSectionRow lable={`specialization 1 title`} value={homeSpecializations.specializations1_title} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 1 description`} value={homeSpecializations.specializations1_description} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 1 icon`} value={homeSpecializations.specializations1_icon} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 2 title`} value={homeSpecializations.specializations2_title} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 2 description`} value={homeSpecializations.specializations2_description} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 2 icon`} value={homeSpecializations.specializations2_icon} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 3 title`} value={homeSpecializations.specializations3_title} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 3 description`} value={homeSpecializations.specializations3_description} isImage={false} isIcon={false} hasChildren={false} />
                            <DetailSectionRow lable={`specialization 3 icon`} value={homeSpecializations.specializations3_icon} isImage={false} isIcon={false} hasChildren={false} />
                        </DetailSectionContent>
                    </DetailSection>
                </PlainContainer>
            </Section>
        </>
    )
}

export default HomeSpecializationsPage

