import React, { useEffect, useState } from 'react';
import { API_BASE_URL } from '../../../config';
import DocumentUploadPopUp from '../../dashboard/popups/DocumentUploadPopUp';

const DocumentInput = ({lable, isMandatory, value, preview, showPreview, onchange, isError, error, reset}) => {

    const [showError, setShowError] = useState(isError);
    const [errorTxt, setErrorTxt] = useState(error);
    const [valueTxt, setValueTxt] = useState(value);
    const [valueId, setValueId] = useState(value);
    const [previewIcon, setPreviewIcon] = useState();

    const [hideFilePopUp, setHideFilePopUp] = useState(true);

    const [isFileAttached, setIsFileAttached] = useState(false);

    useEffect(() => {
        setShowError(isError);
        setErrorTxt(error);
    }, [isError]);

    useEffect(() => {
        setValueId(value);
    }, [value]);

    useEffect(() => {
        setShowError(false);
        setErrorTxt('');
        setValueTxt(value);
        setValueId(value);
        setHideFilePopUp(true);
        setIsFileAttached(false);
    }, [reset]);

    const getUploadedImageUrl = (data) => {
        if (data.url) {
            let fileExt = data.url.split('.').pop();
            console.log(fileExt);
            if(fileExt == "xls" || fileExt == 'xlsx'){
                setValueTxt('assets/images/excel-icon.jpg');
            }
            else if(fileExt == "pdf"){
                setValueTxt('assets/images/pdf-icon.jpg');
            }
            else{
                setValueTxt(data.url);
            }
            
            setValueId(data.id);
            setIsFileAttached(true);
            setHideFilePopUp(true);
            onchange(data.id);
        }
        setHideFilePopUp(true);
    }

    useEffect(() => {
        if(showPreview === true){
            console.log(preview);
            let fileExt = preview.split('.').pop();
            console.log(fileExt);
            if(fileExt == "xls" || fileExt == 'xlsx'){
                setPreviewIcon(`${API_BASE_URL}assets/images/excel-icon.jpg`);
            }
            else if(fileExt == "pdf"){
                setPreviewIcon(`${API_BASE_URL}assets/images/pdf-icon.jpg`);
            }
        }
    }, [showPreview])

    return (
        <>
            <div className='formStepFileSingle'>
                <div className="formStepFile center" onClick={() => { setHideFilePopUp(false) }}>
                    <div className="formStepFileMain">
                        <div className="formStepFileMainIcon">
                            <i className="far fa-image formStepFileMainIco"></i>
                        </div>
                        <div className="formStepFileMainLable hCenter">
                            <p className="formStepFileMainLableTxt">{lable} {isMandatory === true ? <span className="formStepLableTxtCheck">*</span> : <></>}</p>
                        </div>
                    </div>
                    {
                        isFileAttached === true
                            ? 
                            <div className="formStepFileMainPreview">
                                <div className="formStepFileMainPreviewImage center">
                                    <img src={`${API_BASE_URL}${valueTxt}`} className="formStepFileMainPreviewImg" />
                                </div>
                            </div>
                            : 
                            <></>
                    }
                    {
                        showPreview === true
                            ? 
                            <div className="formStepFileMainPreview">
                                <div className="formStepFileMainPreviewImage center">
                                    <img src={`${previewIcon}`} className="formStepFileMainPreviewImg" />
                                </div>
                            </div>
                            : 
                            <></>
                    }
                </div>
                {
                    showError === true
                    ?
                        <div className="formStepError">
                            <p className="formStepErrorTxt">{errorTxt}</p>
                        </div>
                    :
                        <></>
                }
            </div>
            
            {
                hideFilePopUp === false
                ?
                <DocumentUploadPopUp lable="Upload PDF" primaryLable="upload" primaryAction={(e) => getUploadedImageUrl(e)} secondaryType="danger" secondaryLable="cancel" secondaryAction={() => setHideFilePopUp(true)} getUploadedImageUrl={(e) => getUploadedImageUrl(e)} />
                :
                <></>
            }
        </>
    )
}

export default DocumentInput