import React, { useState, useEffect, useRef, forwardRef} from 'react';
import { useLocation, useNavigate, useParams } from "react-router-dom";

import PlainContainer from '../../../../../../components/general/containers/PlainContainer';
import SplitContainer from '../../../../../../components/general/containers/SplitContainer';
import Section from '../../../../../../components/general/containers/Section';
import Form from '../../../../../../components/general/forms/core/Form';
import FormActions from '../../../../../../components/general/forms/core/FormActions';
import TextInput from '../../../../../../components/general/forms/TextInput';
import DocumentInput from '../../../../../../components/general/forms/DocumentInput';
import TextArea from '../../../../../../components/general/forms/TextArea';
import Button from '../../../../../../components/general/forms/Button';

import axios from 'axios';

import { useSelector } from 'react-redux';
import { API_BASE_URL, LOGGER } from '../../../../../../config';
import { notifyError, notifySuccess } from '../../../../../../utils/toastify';
import { CONTACT_BROCHURE_UPDATED, REQUEST_ERROR } from '../../../../../../utils/messages';

const EditContactUsBrochurePage = () => {
    let navigate = useNavigate();
    const location = useLocation();

    const [routeArray, setRouteArray] = useState(location.pathname.substring(1).split('/'));
    const [routeText, setRouteText] = useState('');

    useEffect(() => {
        let tempRoute = '';
        routeArray.map(route => {
            tempRoute = tempRoute.concat(`${route} / `);
            tempRoute = tempRoute.replace('-', ' ');
        });
        setRouteText(removeLastChars(tempRoute, 3));
    }, []);

    let removeLastChars = (str, chars) => {
        return str.substring(0, str.length - chars);
    }

    const admin = useSelector((state) => state.admin.value);

    const config = {
        headers: {
        'Authorization': admin.access_token
        }
    }

    const [sectionHeading, setSectionHeading] = useState('');
    const [description, setDescription] = useState('');
    const [pdf, setPdf] = useState('');
    const [pdfID, setPdfID] = useState('');
    const [pdfPreviewURL, setPdfPreviewURL] = useState('');
    const [showPdfPreview, setShowPdfPreview] = useState('');

    const [isSectionHeadingError, setIsSectionHeadingError] = useState(false);
    const [isDescriptionError, setIsDescriptionError] = useState(false);
    const [isPdfError, setIsPdfError] = useState(false);

    const [sectionHeadingErrorTxt, setSectionHeadingErrorTxt] = useState("");
    const [descriptionErrorTxt, setDescriptionErrorTxt] = useState("");
    const [pdfErrorTxt, setPdfErrorTxt] = useState("");

    const [resetAttachment, setResetAttachment] = useState(false);

    const getContactUsBrochureHandler = async () => {
        try {
            const getContactUsBrochureReq = await axios.get(`${API_BASE_URL}contact-us/brochure`);
            console.log(getContactUsBrochureReq);
            setSectionHeading(getContactUsBrochureReq.data.section_heading);
            setDescription(getContactUsBrochureReq.data.description);
            setPdf(getContactUsBrochureReq.data.pdf_url?.url);
            setPdfID(getContactUsBrochureReq.data.pdf_url?.id);
            setPdfPreviewURL(`${API_BASE_URL}assets/images/pdf-icon.jpg`);
            if(getContactUsBrochureReq.data.pdf_url != ""){
                setShowPdfPreview(true);
            }
            else if(getContactUsBrochureReq.data.pdf_url != undefined){
                setShowPdfPreview(true);
            }
            else{
                setShowPdfPreview(false);
            }
        }
        catch (error) {
            if(error.response.status === 404){
                console.log('not found');
            }
        }
    }

    useEffect(() => {
        getContactUsBrochureHandler();
    }, []);

    const submitEditThisForm = async (e) => {
        e.preventDefault();

        if(sectionHeading == ''){
            setSectionHeadingErrorTxt('Section heading is required');
            setIsSectionHeadingError(true);
            return;
        }
        else{
            setSectionHeadingErrorTxt('');
            setIsSectionHeadingError(false);
        }

        if(description == ''){
            setDescriptionErrorTxt('Description is required');
            setIsDescriptionError(true);
            return;
        }
        else{
            setDescriptionErrorTxt('');
            setIsDescriptionError(false);
        }

        if(pdfID == ''){
            setPdfErrorTxt('PDF is required');
            setIsPdfError(true);
            return;
        }
        else{
            setPdfErrorTxt('');
            setIsPdfError(false);
        }

        const data = {
            heading: sectionHeading,
            description: description,
            pdf_url: pdfID
        }

        try {
            const updateContactUsBrochureReq = await axios.patch(`${API_BASE_URL}contact-us/brochure/edit`, data, config);
            notifySuccess(CONTACT_BROCHURE_UPDATED);
            navigate('/dashboard/pages/contact-us/brochure');
            LOGGER &&  console.log(updateContactUsBrochureReq);
        }
        catch (error) {
            notifyError(REQUEST_ERROR);
            LOGGER &&  console.log(error);
        }
    }

    const resetForm = () => {
        
    }

    return (
        <Section heading="Brochure" description={routeText} action={() => navigate("/dashboard/pages/contact-us/brochure")} actionLable="back" showAction={true}>
            <PlainContainer styles={{width: '100%', display: 'flex', flexDirection: 'column', position: 'static'}}>
                <form onSubmit={(e) => submitEditThisForm(e)} noValidate>
                    <Form>
                        <TextInput lable="section heading" isMandatory={true} isError={isSectionHeadingError} error={sectionHeadingErrorTxt}>
                            <input type="text" className="formStepInputTxt capitalize" value={sectionHeading} placeholder="Please Enter The Section Heading" onChange={(e) => setSectionHeading(e.target.value)} />
                        </TextInput>
                        <TextArea lable="description" rows={3} isMandatory={false} placeholder="Please Enter The Description" value={description} onchange={(data) => setDescription(data)} isError={isDescriptionError} error={descriptionErrorTxt} />
                        <DocumentInput lable="pdf" isMandatory={true} value={pdfID} preview={pdfPreviewURL} showPreview={showPdfPreview} onchange={(data) => {setPdfID(data); LOGGER &&  console.log(data)}} isError={isPdfError} error={pdfErrorTxt} reset={resetAttachment} />
                        <FormActions isPrimary={true} isSecondary={true} primaryLable="submit" secondaryLable="reset" secondaryAction={() => resetForm()} />
                    </Form>
                </form>
            </PlainContainer>
        </Section>
    )
}

export default EditContactUsBrochurePage