import React, { useState, useEffect, useRef, forwardRef} from 'react';
import { useParams, useLocation, useNavigate } from 'react-router-dom';

import PlainContainer from '../../../../../../components/general/containers/PlainContainer';
import SplitContainer from '../../../../../../components/general/containers/SplitContainer';
import Section from '../../../../../../components/general/containers/Section';
import Form from '../../../../../../components/general/forms/core/Form';
import FormActions from '../../../../../../components/general/forms/core/FormActions';
import TextInput from '../../../../../../components/general/forms/TextInput';
import TextArea from '../../../../../../components/general/forms/TextArea';

import axios from 'axios';

import { useSelector } from 'react-redux';
import { API_BASE_URL, LOGGER } from '../../../../../../config';
import { notifyError, notifySuccess } from '../../../../../../utils/toastify';
import { REQUEST_ERROR, OPENING_UPDATED } from '../../../../../../utils/messages';

const EditHomeTestimonyPage = () => {

    let navigate = useNavigate();
    const location = useLocation();
    let { testimony_id } = useParams(); 

    const [routeArray, setRouteArray] = useState(location.pathname.substring(1).split('/'));
    const [routeText, setRouteText] = useState('');

    useEffect(() => {
        let tempRoute = '';
        routeArray.map(route => {
            tempRoute = tempRoute.concat(`${route} / `);
            tempRoute = tempRoute.replace('-', ' ');
        });
        setRouteText(removeLastChars(tempRoute, 3));
    }, []);

    let removeLastChars = (str, chars) => {
        return str.substring(0, str.length - chars);
    }

    const admin = useSelector((state) => state.admin.value);

    const config = {
        headers: {
        'Authorization': admin.access_token
        }
    }

    const [name, setName] = useState('');
    const [city, setCity] = useState('');
    const [description, setDescription] = useState('');

    const [isNameError, setIsNameError] = useState(false);
    const [isCityError, setIsCityError] = useState(false);
    const [isDescriptionError, setIsDescriptionError] = useState(false);

    const [nameErrorTxt, setNameErrorTxt] = useState("");
    const [cityErrorTxt, setCityErrorTxt] = useState("");
    const [descriptionErrorTxt, setDescriptionErrorTxt] = useState("");

    const getThisTestimonyHandler = async () => {
        try {
            const getThisTestimonyReq = await axios.get(`${API_BASE_URL}home/testimonials/${testimony_id}`);
            console.log(getThisTestimonyReq.data);
            setName(getThisTestimonyReq.data.name);
            setCity(getThisTestimonyReq.data.location);
            setDescription(getThisTestimonyReq.data.description);
        }
        catch (error) {
            if(error.response.status === 404){
                console.log('not found');
            }
        }
    }

    useEffect(() => {
        getThisTestimonyHandler();
    }, []);

    const submitEditThisForm = async (e) => {
        e.preventDefault();

        if(name == ''){
            setNameErrorTxt('Name is required');
            setIsNameError(true);
            return;
        }
        else{
            setNameErrorTxt('');
            setIsNameError(false);
        }

        if(city == ''){
            setCityErrorTxt('Location is required');
            setIsCityError(true);
            return;
        }
        else{
            setCityErrorTxt('');
            setIsCityError(false);
        }

        if(description == ''){
            setDescriptionErrorTxt('Testimony is required');
            setIsDescriptionError(true);
            return;
        }
        else{
            setDescriptionErrorTxt('');
            setIsDescriptionError(false);
        }

        const data = {
            name: name,
            location: city,
            description: description
        }

        try {
            const updateTestimonyReq = await axios.patch(`${API_BASE_URL}home/testimonials/${testimony_id}`, data, config);
            notifySuccess(OPENING_UPDATED);
            navigate('/dashboard/pages/home/testimonials');
            LOGGER &&  console.log(updateTestimonyReq);
        }
        catch (error) {
            notifyError(REQUEST_ERROR);
            LOGGER &&  console.log(error);
        }
    }

    const resetForm = () => {
        
    }

    return (
        <Section heading="Testimonials" description={routeText} action={() => navigate("/dashboard/pages/home/testimonials")} actionLable="back" showAction={true}>
            <PlainContainer styles={{width: '100%', display: 'flex', flexDirection: 'column', position: 'static'}}>
                <form onSubmit={(e) => submitEditThisForm(e)} noValidate>
                    <Form>
                        <SplitContainer type={`fullwidth`}>
                            <TextInput lable="name" isMandatory={true} isError={isNameError} error={nameErrorTxt}>
                                <input type="text" className="formStepInputTxt capitalize" value={name} placeholder="Please Enter The Name" onChange={(e) => setName(e.target.value)} />
                            </TextInput>
                            <TextInput lable="location" isMandatory={true} isError={isCityError} error={cityErrorTxt}>
                                <input type="text" className="formStepInputTxt capitalize" value={city} placeholder="Please Enter The Location" onChange={(e) => setCity(e.target.value)} />
                            </TextInput>
                        </SplitContainer>
                        <TextArea lable="description" rows={3} isMandatory={true} placeholder="Please Enter The Description" value={description} onchange={(data) => setDescription(data)} isError={isDescriptionError} error={descriptionErrorTxt} />
                        <FormActions isPrimary={true} isSecondary={true} primaryLable="submit" secondaryLable="reset" secondaryAction={() => resetForm()} />
                    </Form>
                </form>
            </PlainContainer>
        </Section>
    )
}

export default EditHomeTestimonyPage